<template>
  <form @submit="onSubmint" class="add-form">
    <div class="form-control">
      <label>Tarefa</label>
      <input
        type="text"
        v-model="text"
        name="text"
        placeholder="Adicione tarefa"
      />
    </div>
    <div class="form-control">
      <label>Data e Horário</label>
      <input
        type="text"
        v-model="day"
        name="day"
        placeholder="Adicione data e horário"
      />
    </div>
    <div class="form-control form-control-check">
      <label>Lembrar</label>
      <input type="checkbox" v-model="reminder" name="reminder" />
    </div>

    <input type="submit" value="Salvar Tarefa" class="btn btn-block" />
  </form>
</template>

<script>
export default {
  name: "AddTask",
  data() {
    return {
      text: "",
      day: "",
      reminder: false,
    };
  },
  methods: {
    onSubmint(e) {
      e.preventDefault();
      if (!this.text) {
        alert("Preencha o campo Tarefa");
        return;
      }
      if (!this.day) {
        alert("Preencha o campo Data e Hora");
        return;
      }

      const newTask = {
        id: Math.floor(Math.random() * 10000),
        text: this.text,
        day: this.day,
        reminder: this.reminder,
      };

      this.$emit('add-task', newTask);

      this.text = "";
      this.day = "";
      this.reminder = "";
    },
  },
};
</script>

