<template>
  <div class="container">
    <AppHeader @toggle-add-task="toggleAddTask" :showAddTask="showAddTask" title="Rastreado de Despesa" />
    <div v-show="showAddTask" >
      <AddTask @add-task="addTask" />
    </div>
    <AppTasks
      @toggle-reminder="toggleReminder"
      @delete-task="deleteTask"
      :tasks="tasks"
    />
  </div>
</template>

<script>
import AppHeader from "./components/Header";
import AppTasks from "./components/Tasks";
import AddTask from "./components/AddTask.vue";

export default {
  name: "App",
  components: {
    AppHeader,
    AppTasks,
    AddTask,
  },
  data() {
    return {
      tasks: [],
      showAddTask: false,
    };
  },
  methods: {
    addTask(task) {
      this.tasks = [...this.tasks, task];
    },
    deleteTask(id) {
      if (confirm("Deletar Tarefa?")) {
        this.tasks = this.tasks.filter((task) => task.id !== id);
      }
    },
    toggleReminder(id) {
      this.tasks = this.tasks.map((task) =>
        task.id === id ? { ...task, reminder: !task.reminder } : task
      );
    },
    toggleAddTask(){
      this.showAddTask = !this.showAddTask;
    }
  },
  created() {
    this.tasks = [
      {
        id: 1,
        text: "Consulta médica",
        day: "1 Mar 2:30pm",
        reminder: false,
      },
      {
        id: 2,
        text: "Encontro na Escola",
        day: "2 Mar 3:30pm",
        reminder: true,
      },
      {
        id: 3,
        text: "Compras de casa",
        day: "3 Mar 4:30pm",
        reminder: true,
      },
    ];
  },
};
</script>

